/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* Form Validation */

:root {
  --red-900: #b71c1c;
  --green-900: #1b5e20;
  --gray-400: #bdbdbd;
  --gray-900: #212121;
  --white: #fff;
}

body {
  font-family:
    Inclusive Sans,
    Arial,
    Helvetica,
    sans-serif;
  color: var(--gray-900);
}

form {
  max-width: 500px;
  width: 100%;
  padding: 20px;
  margin: 0 auto;
}

form h1 {
  margin-bottom: 20px;
  text-align: center;
}

.form-row {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

form label {
  font-weight: bold;
  margin-bottom: 5px;
}

form input {
  border-radius: 10px;
  padding: 5px 10px;
  border: 2px solid var(--gray-400);
}

form button {
  background: none;
  display: block;
  margin-left: auto;
  border: 2px solid var(--gray-400);
  border-radius: 10px;
  padding: 5px 10px;
  cursor: pointer;
}

/* Error */

input:user-invalid {
  border-color: var(--red-900);
}

span.error {
  color: var(--red-900);
}

.checks {
  list-style: none;
  padding-left: 0;
}

.check {
  display: flex;
  align-items: center;
  gap: 5px;
}

.check::before {
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: var(--gray-400);
}

/* Success */

.check.green {
  color: var(--green-900);
}

.check.green::before {
  height: 15px;
  width: 15px;
  border-radius: 0;
  background-color: var(--white);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  background-repeat: no-repeat;
  background-position: center;
  background-size: 15px;
}

.success {
  display: block;
  text-align: right;
  color: var(--green-900);
}
